import { FormulaPageModule } from './../pages/formula/formula.module';
import { PlanetsPage } from './../pages/planets/planets';
import { ConstPage } from './../pages/const/const';
import { LightPage } from './../pages/light/light';
import { ElectroPage } from './../pages/electro/electro';
import { MechanicPage } from './../pages/mechanic/mechanic';
import { PlanetsPageModule } from './../pages/planets/planets.module';
import { ConstPageModule } from './../pages/const/const.module';
import { LightPageModule } from './../pages/light/light.module';
import { ElectroPageModule } from './../pages/electro/electro.module';
import { MechanicPageModule } from './../pages/mechanic/mechanic.module';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { AdMobFree } from '@ionic-native/admob-free';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { FormulaPage } from '../pages/formula/formula';

@NgModule({
  declarations: [
    MyApp,
    HomePage
  ],
  imports: [
    BrowserModule,
    MechanicPageModule,
    ElectroPageModule,
    LightPageModule,
    ConstPageModule,
    PlanetsPageModule,
    FormulaPageModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/formula/formula.module#FormulaPageModule', name: 'FormulaPage', segment: 'formula', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/planets/planets.module#PlanetsPageModule', name: 'PlanetsPage', segment: 'planets', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/const/const.module#ConstPageModule', name: 'ConstPage', segment: 'const', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/light/light.module#LightPageModule', name: 'LightPage', segment: 'light', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/electro/electro.module#ElectroPageModule', name: 'ElectroPage', segment: 'electro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/mechanic/mechanic.module#MechanicPageModule', name: 'MechanicPage', segment: 'mechanic', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    MechanicPage,
    ElectroPage,
    LightPage,
    ConstPage,
    PlanetsPage,
    FormulaPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AdMobFree,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



